const OPENAI_API_KEY = process.env.REACT_APP_OPENAI_API_KEY;
const OPENAI_API_URL = 'https://api.openai.com/v1/chat/completions';

export const generateChartInsight = async (chartData, chartType, chartTitle) => {
  try {
    if (!OPENAI_API_KEY) {
      console.warn('OpenAI API key not found. Please set REACT_APP_OPENAI_API_KEY in your environment variables.');
      return null;
    }

    // Validate input data
    if (!chartData || !chartType || !chartTitle) {
      console.warn('Invalid chart data provided for insight generation');
      return null;
    }

    // Prepare the data for analysis
    const dataSummary = {
      chartType,
      chartTitle,
      data: chartData
    };

    const prompt = `Analyze this chart data and provide a brief, insightful comment about the visualization. Focus on key trends, patterns, or notable observations.

Chart Information:
- Type: ${chartType}
- Title: ${chartTitle}
- Data: ${JSON.stringify(dataSummary.data, null, 2)}

Please provide a concise insight (1-2 sentences) that would be helpful for someone hovering over this chart. Focus on business intelligence and actionable observations.`;

    // Create AbortController for timeout
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 10000); // 10 second timeout

    const response = await fetch(OPENAI_API_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${OPENAI_API_KEY}`
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: 'You are a data analyst providing brief, insightful comments about business charts and visualizations. Keep responses concise and focused on key business insights.'
          },
          {
            role: 'user',
            content: prompt
          }
        ],
        max_tokens: 150,
        temperature: 0.7
      }),
      signal: controller.signal
    });

    clearTimeout(timeoutId);

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`OpenAI API error: ${response.status} - ${errorText}`);
    }

    const result = await response.json();
    
    // Validate response structure
    if (!result.choices || !result.choices[0] || !result.choices[0].message) {
      throw new Error('Invalid response structure from OpenAI API');
    }

    return result.choices[0].message.content?.trim() || null;

  } catch (error) {
    if (error.name === 'AbortError') {
      console.error('OpenAI API request timed out');
    } else {
      console.error('Error generating chart insight:', error);
    }
    return null;
  }
};

