    'Profitability Ratio per Category': 'This radar chart displays the profitability ratio for each category, highlighting which categories generate the best returns.',
    'Total Revenue Over Time': 'This line chart tracks revenue trends over time, showing sales performance and identifying peak periods.',
    'Average Selling Price per Product': 'This bar chart compares average prices across products, revealing pricing strategies and market positioning.',
    'Top Categories by Total Revenue': 'This doughnut chart shows revenue distribution by category, highlighting the most valuable product segments.',
    'Price Distribution Analysis': 'This histogram reveals the price range distribution, showing where most products are positioned in the market.',
    'Profit Margin per Product': 'This chart displays profit margins for each product, identifying high and low-margin items.',
    'Total Profit Over Time': 'This line chart tracks profit trends, showing profitability patterns and seasonal variations.',
    'Most Profitable Categories': 'This horizontal bar chart ranks categories by total profit, highlighting the most lucrative segments.',
    'Profit vs Sales Correlation': 'This scatter plot shows the relationship between sales volume and profit, revealing efficiency patterns.',
    'Quantity Sold per Product': 'This chart shows sales volume by product, identifying best-sellers and slow-moving items.',
    'Product Performance Comparison': 'This comparison chart shows multiple metrics for each product, providing a comprehensive view.',
    'Sales Trend Analysis': 'This trend analysis reveals patterns in sales performance over time.',
    'Geographic Sales Distribution': 'This map shows sales distribution across different locations, highlighting regional performance.',
    'Sales by Location': 'This chart compares sales performance across different geographic locations.',
    'Profit by Location': 'This chart shows profitability by location, revealing regional profit patterns.',
    'Location Performance Comparison': 'This comparison chart shows both sales and profit by location.',
